   
   #nameTag{ 
   padding: 0.5em;
   padding-left: 1.25em;
   padding-right: 1.25em;
   border-radius: 20px;
   background-color: #1A237E;
   display: inline;
   -webkit-transition: color 2s;
   -o-transition: color 2s;
   transition: color 2s;
   background: -webkit-gradient(linear, left top, right top, from(#C33764), to(#252E73));
   background: -webkit-linear-gradient(left, #C33764 0%, #252E73 100%);
   background: -o-linear-gradient(left, #C33764 0%, #252E73 100%);
   background: linear-gradient(90deg, #C33764 0%, #252E73 100%);
  background-size: 400% 400%;
  -webkit-animation: BackgroundGradient 25s ease infinite;
          animation: BackgroundGradient 25s ease infinite;
  position: absolute;
  right: 1em;
  display: inline;
  -webkit-transition: color 1s;
  -o-transition: color 1s;
  transition: color 1s;
   }

  
   #nameTag:hover{
    background-color: #283593;
    cursor: pointer;
  }
  @-webkit-keyframes BackgroundGradient {
    0% {background-position: 0% 50%;}
    50% {background-position: 100% 50%;}
    100% {background-position: 0% 50%;}
  }
  @keyframes BackgroundGradient {
    0% {background-position: 0% 50%;}
    50% {background-position: 100% 50%;}
    100% {background-position: 0% 50%;}
  }


